import { HUtils, MyIcon, MyUI } from "@macashipo/mlib"
import cx from "classnames"
import React from "react"
import { Button } from "reactstrap"
import { PopoverBoostrap } from "src/core/components"
import V from "src/helpers/V"
import { KConfigPageHeader } from "src/helpers/K"
import { fnListMyPage } from "src/helpers/fnList"
import styled from "styled-components"

const WrapFilter = styled.div`
  background: white;
  margin-top: 5px;
  border-radius: 5px;
  box-shadow: 0 7px 14px 0 rgb(65 69 88 / 10%), 0 3px 6px 0 rgb(0 0 0 / 7%);
`
export default function MUIATHeader(props) {
  const { cfp } = props
  const enablePageTitleIcon = true
  const enablePageTitleSubheading = true
  const configPage = props.configPage
  let _configFilter = fnListMyPage.fnGetConfigPageFilter(cfp.fnList) || {}
  let _showFilter = V.TableFilter_IsShowFilter(cfp.onePage)
  let _showFilterAsPopover =
    _configFilter.showFilterAsPopover != null
      ? _configFilter.showFilterAsPopover
      : true
  let _showSearch = V.TableFilter_IsShowSearch(cfp.onePage)
  let _configHeader = fnListMyPage.fnGetConfigPageHeader(cfp.fnList) || {}
  let _title = _configHeader.title || configPage.Title
  return (
    <div
      className={cx("mpat-header app-page-title", {
        "mpat-header-sticky":
          cfp.onePage._oneScreenConfig._header.sticky === true,
      })}
    >
      <div className="page-title-wrapper">
        <div className="page-title-heading">
          <div>
            {_configHeader[KConfigPageHeader.hideTitle] !== true && _title}
            <div
              className={cx("page-title-subheading", {
                "no-print": true,
                "d-none": !enablePageTitleSubheading,
              })}
              style={{
                display: "flex",
                flexDirection: "row",
                alignItems: "center",
              }}
            >
              {/* Subtitle */}
              {_showFilter === true && _showFilterAsPopover == true && (
                <PopoverBoostrap
                  styleWrap={{ marginRight: "10px" }}
                  targetId="popover-filter"
                  overlay={propsOverlay => {
                    console.warn("render Popover", propsOverlay)
                    return (
                      <MyUI
                        type="admintable_filter_popover"
                        {...propsOverlay}
                        cfp={cfp}
                      />
                    )
                  }}
                >
                  <Button color="primary" style={{ borderRadius: "20px" }}>
                    <i className="fa fa-filter" />
                  </Button>
                </PopoverBoostrap>
              )}
              {_showSearch === true && (
                <MyUI
                  type="search_box"
                  onSearch={textSearch => {
                    HUtils.runFuntion(cfp, "onSearch", [textSearch])
                  }}
                />
              )}
            </div>
          </div>
        </div>
        <div className="page-title-actions no-print">
          <MyUI {...props} type="admintable_header_buttons" />
        </div>
      </div>
      {_showFilterAsPopover === false && (
        <WrapFilter className="no-print">
          <MyUI
            type="admintable_filter_popover"
            cfp={cfp}
            withoutPopover={true}
          />
        </WrapFilter>
      )}
    </div>
  )
}
